"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { Video, Settings, Mail, Info } from "lucide-react";
import {
  Sidebar,
  SidebarHeader,
  SidebarContent,
  SidebarMenu,
  SidebarMenuItem,
  SidebarMenuButton,
  useSidebar,
} from "@/components/ui/sidebar";
import { Button } from "./ui/button";
import Logo from "./Logo";

const menuItems = [
  { href: "/dashboard", label: "Videos", icon: Video },
  { href: "/contact", label: "Contact", icon: Mail },
  { href: "/about", label: "About", icon: Info },
  { href: "/settings", label: "Settings", icon: Settings },
];

export default function AppSidebar({ isAuthenticated }: { isAuthenticated: boolean }) {
  const pathname = usePathname();
  const { open } = useSidebar();

  return (
    <Sidebar>
      <SidebarHeader>
        <Link href="/dashboard" className="flex items-center gap-2">
          <Logo className="h-8 w-8" />
          {open && <h1 className="text-xl font-semibold">Resurrection Gospel</h1>}
        </Link>
      </SidebarHeader>
      <SidebarContent>
        <SidebarMenu>
          {menuItems.map((item) => (
            (!isAuthenticated && (item.href === '/settings')) ? null :
            <SidebarMenuItem key={item.href}>
              <SidebarMenuButton
                asChild
                isActive={pathname === item.href || (item.href !== "/dashboard" && pathname.startsWith(item.href))}
                className="w-full justify-start"
                tooltip={item.label}
              >
                <Link href={item.href}>
                  <item.icon className="h-5 w-5" />
                  {open && <span>{item.label}</span>}
                </Link>
              </SidebarMenuButton>
            </SidebarMenuItem>
          ))}
        </SidebarMenu>
      </SidebarContent>
      {!isAuthenticated && open && <div className="p-4">
          <div className="flex flex-col gap-2 rounded-lg border p-4">
            <p className="text-sm font-semibold">Join Resurrection Gospel</p>
            <p className="text-xs text-muted-foreground">Sign up to comment, like, and connect your own accounts.</p>
            <Button asChild size="sm">
              <Link href="/signup">Sign Up</Link>
            </Button>
            <Button asChild variant="secondary" size="sm">
              <Link href="/login">Log In</Link>
            </Button>
          </div>
        </div>}
    </Sidebar>
  );
}
